import React, { useState } from "react";
import btnExpandir from './hardware2bar.png';
import gatlingLogo from './gatlingBackgroundTransparente.png';
import './App.css';



function Collapsible(props) {
    const [isOpen, setIsOpen] = useState(false);

    return <div classname="collapsible">
        <button type="submit" className="btnExpandFormat" onClick={() => setIsOpen(!isOpen)}>
            {props.hardware}
        </button>
        {isOpen && <div className="esconder">{props.children}
        </div>
        }
    </div>
}

function CollapsibleTwo(props) {
    const [isOpen, setIsOpen] = useState(false);

    return <div classname="collapsibletwo">
        <button type="submit" className="btnExpandFormat" onClick={() => setIsOpen(!isOpen)}>
            SSSSSSSSSSS
        </button>
        {isOpen && <div className="esconder">{props.children}
        </div>
        }
    </div>
}

export default Collapsible;
