import { useState } from 'react';
import './App.css';
import gatlingLogo from './gatlingBackgroundTransparente.png';
import ReactTooltip from 'react-tooltip';
import Collapsible from './Collapsible';
import CollapsibleTwo from './Collapsible';

function App() {
  const [link, setLink] = useState('https://vivinicius.github.io/performanceResults/users_200_9/index.html');
  const [showResults, setShowResult] = useState(false);
  const [buttonSelected, setButtonSelected] = useState('');

  function handleLink(params) {
    !showResults && setShowResult(true);
    setLink(`https://vivinicius.github.io/performanceResults/${params}/index.html`);
    setButtonSelected(params);
  };


  return (
    <>
      <div class="tittle">
        <h1>RESULTADOS DOS TESTES DE PERFORMANCE MAXXIFORGE</h1>
      </div>

      <div class="container">
        <div class="allTest">
          <div class="test1">
            <div class="hardware1">
              <div class="topbar">
                <br />
              </div>
              <br />
              <strong>HARDWARE:</strong>
              <br /><strong>API:</strong> 0,5x (CPU) 1GB(RAM) <br />
              <strong>REDIS T3.MICRO:</strong> 0,5GB(RAM) <br />
              <strong>BANCO DE DADOS:</strong> 2xCPU 2GB(RAM) <br />
            </div>
            <Collapsible hardware="EXPANDIR">
              <ReactTooltip />
              <div class="testTittle1">
                <strong>CENARIO 1:</strong>
              </div>
              <div class="testDescription1">
                <br />Esse teste foi executado da forma que mantemos X usuarios<br />
                conectados na aplicação durante 5 minutos, os usuarios ficaram <br />
                realizando toda a jornada em looping durante todo o tempo de<br />
                execução dos testes.<br /> <br />
              </div>

              <div class="links1">
                <button onClick={() => handleLink('10_usrs_5_min_11')} className={`linkButton ${buttonSelected === '10_usrs_5_min_11' && 'selected'}`}>10 Usuários</button>
                <button onClick={() => handleLink('20_usrs_5_min_11')} className={`linkButton ${buttonSelected === '20_usrs_5_min_11' && 'selected'}`} >20 Usuários</button>
                <button onClick={() => handleLink('30_usrs_5_min_11')} className={`linkButton ${buttonSelected === '30_usrs_5_min_11' && 'selected'}`} >30 Usuários</button>
                <button onClick={() => handleLink('40_usrs_5_min_11')} data-tip="Quantidade de usuários recomendados para o Hardware" className={`linkButtonRecomend ${buttonSelected === '40_usrs_5_min_11' && 'selectedHardware'}`} >40 Usuários</button>
                <button onClick={() => handleLink('50_usrs_5_min_11')} className={`linkButton ${buttonSelected === '50_usrs_5_min_11' && 'selected'}`} >50 Usuários</button>
                <button onClick={() => handleLink('100_usrs_5_min_11')} className={`linkButton ${buttonSelected === '100_usrs_5_min_11' && 'selected'}`}>100 Usuários</button>
              </div>
              <div class="testTittle2">
                <strong>CENARIO 2:</strong>
              </div>
              <div class="testDescription2">
                <br />Esse teste foi executado da forma que subimos X usuarios de uma<br />
                só vez na aplicação, a jornada toda é executada apenas uma vez.<br /> <br />
              </div>

              <div class="links2">
                <button onClick={() => handleLink('10_users_1x_11')} className={`linkButton ${buttonSelected === '10_users_1x_11' && 'selected'}`}>10 Usuários</button>
                <button onClick={() => handleLink('20_users_1x_11')} className={`linkButton ${buttonSelected === '20_users_1x_11' && 'selected'}`}>20 Usuários</button>
                <button onClick={() => handleLink('40_users_1x_11')} className={`linkButton ${buttonSelected === '40_users_1x_11' && 'selected'}`}>40 Usuários</button>
                <button onClick={() => handleLink('60_users_1x_11')} className={`linkButton ${buttonSelected === '60_users_1x_11' && 'selected'}`}>60 Usuários</button>
                <button onClick={() => handleLink('80_users_1x_11')} className={`linkButton ${buttonSelected === '80_users_1x_11' && 'selected'}`}>80 Usuários</button>
                <button onClick={() => handleLink('100_users_1x_11')} className={`linkButton ${buttonSelected === '100_users_1x_11' && 'selected'}`}>100 Usuários</button>
              </div>

              <div class="testTittle1aws">
                <strong>Teste 1 (AWS):</strong>
              </div>

              <div class="testDescription1Aws">
                <br />Esses testes foram executados da mesma forma que o teste 1, porém  <br />
                usando uma maquina virtual na AWS para desconsiderarmos o fator <br />
                latencia dos testes.<br /><br />
              </div>

              <div class="links1aws">
                <button onClick={() => handleLink('10_usrs_5_min_11_aws')} className={`linkButtonAws ${buttonSelected === '10_usrs_5_min_11_aws' && 'selectedAws'}`}>10 Usuários</button>
                <button onClick={() => handleLink('20_usrs_5_min_11_aws')} className={`linkButtonAws ${buttonSelected === '20_usrs_5_min_11_aws' && 'selectedAws'}`}>20 Usuários</button>
                <button onClick={() => handleLink('30_usrs_5_min_11_aws')} className={`linkButtonAws ${buttonSelected === '30_usrs_5_min_11_aws' && 'selectedAws'}`}>30 Usuários</button>
                <button onClick={() => handleLink('40_usrs_5_min_11_aws')} className={`linkButtonAws ${buttonSelected === '40_usrs_5_min_11_aws' && 'selectedAws'}`}>40 Usuários</button>
                <button onClick={() => handleLink('50_usrs_5_min_11_aws')} className={`linkButtonAws ${buttonSelected === '50_usrs_5_min_11_aws' && 'selectedAws'}`}>50 Usuários</button>
                <button onClick={() => handleLink('100_usrs_5_min_11_aws')} className={`linkButtonAws ${buttonSelected === '100_usrs_5_min_11_aws' && 'selectedAws'}`}>100 Usuários</button>
              </div>

              <div class="testTittle2aws">
                <strong>Teste 2 (AWS):</strong>
              </div>
              <div class="testDescription2aws">
                <br />Esses testes foram executados da mesma forma que o teste 2, porém  <br />
                usando uma maquina virtual na AWS para desconsiderarmos o fator <br />
                latencia dos testes.<br /><br />
              </div>

              <div class="links2aws">
                <button onClick={() => handleLink('10_users_1x_11_aws')} className={`linkButtonAws ${buttonSelected === '10_users_1x_11_aws' && 'selectedAws'}`}>10 Usuários</button>
                <button onClick={() => handleLink('20_users_1x_11_aws')} className={`linkButtonAws ${buttonSelected === '20_users_1x_11_aws' && 'selectedAws'}`}>20 Usuários</button>
                <button onClick={() => handleLink('40_users_1x_11_aws')} className={`linkButtonAws ${buttonSelected === '40_users_1x_11_aws' && 'selectedAws'}`}>40 Usuários</button>
                <button onClick={() => handleLink('60_users_1x_11_aws')} className={`linkButtonAws ${buttonSelected === '60_users_1x_11_aws' && 'selectedAws'}`}>60 Usuários</button>
                <button onClick={() => handleLink('80_users_1x_11_aws')} className={`linkButtonAws ${buttonSelected === '80_users_1x_11_aws' && 'selectedAws'}`}>80 Usuários</button>
                <button onClick={() => handleLink('100_users_1x_11_aws')} className={`linkButtonAws ${buttonSelected === '100_users_1x_11_aws' && 'selectedAws'}`}>100 Usuários</button>
              </div>
            </Collapsible>
          </div>
          <br />
          <div class="test2">
            <div class="hardware1">
              <div class="topbar">
                <br />
              </div>
              <br /><strong>HARDWARE:</strong>
              <br /><strong>API:</strong> 1x (CPU) 2GB(RAM) <br />
              <strong>REDIS T3.MICRO:</strong> 0,5GB(RAM) <br />
              <strong>BANCO DE DADOS:</strong> 2xCPU 4GB(RAM) <br />
            </div>
            <Collapsible hardware="EXPANDIR">
              <ReactTooltip />
              <div class="testTittle1">
                <strong>CENARIO 1:</strong>
              </div>
              <div class="testDescription1">
                <br />Esse teste foi executado da forma que mantemos X usuarios<br />
                conectados na aplicação durante 5 minutos, os usuarios ficaram <br />
                realizando toda a jornada em looping durante todo o tempo de<br />
                execução dos testes.<br /> <br />
              </div>

              <div class="links1">
                <button onClick={() => handleLink('30_usrs_5_min_12')} className={`linkButton ${buttonSelected === '30_usrs_5_min_12' && 'selected'}`}>30 Usuários</button>
                <button onClick={() => handleLink('40_usrs_5_min_12')} className={`linkButton ${buttonSelected === '40_usrs_5_min_12' && 'selected'}`}>40 Usuários</button>
                <button onClick={() => handleLink('50_usrs_5_min_12')} className={`linkButton ${buttonSelected === '50_usrs_5_min_12' && 'selected'}`}>50 Usuários</button>
                <button onClick={() => handleLink('60_usrs_5_min_12')} data-tip="Quantidade de usuários recomendados para o Hardware" className={`linkButtonRecomend ${buttonSelected === '60_usrs_5_min_12' && 'selectedHardware'}`} >60 Usuários</button>
                <button onClick={() => handleLink('70_usrs_5_min_12')} className={`linkButton ${buttonSelected === '70_usrs_5_min_12' && 'selected'}`}>70 Usuários</button>
                <button onClick={() => handleLink('100_usrs_5_min_12')} className={`linkButton ${buttonSelected === '100_usrs_5_min_12' && 'selected'}`}>100 Usuários</button>
              </div>

              <div class="testTittle2">
                <strong>CENARIO 2:</strong>
              </div>
              <div class="testDescription2">
                <br />Esse teste foi executado da forma que subimos X usuarios de uma<br />
                só vez na aplicação, a jornada toda é executada apenas uma vez.<br /> <br />
              </div>

              <div class="links2">
                <button onClick={() => handleLink('30_users_1x_12')} className={`linkButton ${buttonSelected === '30_users_1x_12' && 'selected'}`}>30 Usuários</button>
                <button onClick={() => handleLink('50_users_1x_12')} className={`linkButton ${buttonSelected === '50_users_1x_12' && 'selected'}`}>50 Usuários</button>
                <button onClick={() => handleLink('70_users_1x_12')} className={`linkButton ${buttonSelected === '70_users_1x_12' && 'selected'}`}>70 Usuários</button>
                <button onClick={() => handleLink('90_users_1x_12')} className={`linkButton ${buttonSelected === '90_users_1x_12' && 'selected'}`}>90 Usuários</button>
                <button onClick={() => handleLink('110_users_1x_12')} className={`linkButton ${buttonSelected === '110_users_1x_12' && 'selected'}`}>110 Usuários</button>
                <button onClick={() => handleLink('130_users_1x_12')} className={`linkButton ${buttonSelected === '130_users_1x_12' && 'selected'}`}>130 Usuários</button>
              </div>
            </Collapsible>
          </div>
        </div>
        {showResults ? (
          <div class="linkGatling">
            <iframe src={link} width="40%" height="500px" />
          </div>
        ) : (
          <div class="gatlingBackgroundImage">
            <img src={gatlingLogo} />
          </div>
        )}
      </div>
    </>
  );
}

export default App;
